import { styled } from '@mui/material/styles';
import { colors } from '@/theme/colors';

export const classes = {
  iconActive: 'iconActive',
  iconInactive: 'iconInactive',
  textActive: 'textActive',
  textInactive: 'textInactive',
};

export const StyledBottomNavigation = styled('nav')(({ theme }) => ({
  position: 'fixed',
  width: '100%',
  right: 0,
  left: 0,
  bottom: 0,
  display: 'flex',
  justifyContent: 'space-around',
  alignItems: 'center',
  zIndex: 10,
  borderStyle: 'solid',
  borderWidth: '1px 0 0 0',
  borderColor: '#E5E7EB',
  backgroundColor: '#FFFFFF',
  padding: '0.5rem 1rem',

  [`& .${classes.iconActive}`]: {
    color: '#333',
  },
  [`& .${classes.iconInactive}`]: {
    color: '#888',
  },

  [`& .${classes.textActive}`]: {
    color: '#333',
    fontWeight: 600,
  },
  [`& .${classes.textInactive}`]: {
    color: '#aaa',
  },
}));

export default StyledBottomNavigation;
